.formInp{
    display: flex;
    flex-direction: column;
    border: solid 2px black;
    width: 45%;
    padding: 10px;
}
input{
    width: 200px;
    
    border: solid 1px rgb(32, 30, 30) ;
    border-radius: 4px;
    padding: 6px;
    margin-left: 7px;

}
.label{
    color: black;
    font-weight: 600;
font-size: 18px;
line-height: 1.67;
letter-spacing: 0.03em;
margin-bottom: 15px;

}

.btn{
    display: block;
    width: 100px;
    margin-top: 20px;
    margin-left: 50px;
    background-color: rgb(215, 231, 250);
    border-radius: 5px;
    padding: 5px;
    border: none;
    cursor: pointer;

}